	module.exports = {"data":{"talkJson":{"description":"\"Speaker: Tom Eastman\n\nIt’s not in the OWASP Top 10, but you don’t have to look far to hear stories of security vulnerabilities involving deserialization of user input. In this talk I’ll go over what the threat is and how you might be making yourself vulnerable. I’ll cover the features (not bugs: features) of XML, YAML, and JSON that make them surprisingly dangerous, and how to protect your code from them.\n\nSlides can be found at: https://speakerdeck.com/pycon2015 and https://github.com/PyCon/2015-slides\"","meta":{"duration":"PT29M54S"},"presenter":{"name":null},"slug":"tom-eastman-serialization-formats-are-not-toys-pycon-2015","source":{"id":"kjZHjvrAS74","provider":"youtube"},"tag":[],"title":"Tom Eastman - Serialization formats are not toys - PyCon 2015"},"allTalkJson":null},"pathContext":{"slug":"tom-eastman-serialization-formats-are-not-toys-pycon-2015","tags":[]}}

